@import url(https://fonts.googleapis.com/css?family=Dosis:400,700);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  font: 16px/1.5 "Dosis", sans-serif;
  background-color: #22254b;
  color: white;
  margin: 0;
}

/* CONTAINER
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.main-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}

.main-container > * {
  padding: 20px;
}

/* MAIN
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.page-main {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  min-height: 250px;
  border-radius: 10px;
  box-shadow: 0 0 10px 10px rgba(0 0 0 0.25);
}

.page-main h1 {
  margin-bottom: 20px;
}

.page-main p + p {
  margin-top: 10px;
}

