.movie_details {
  height: 600px;
  margin: 2rem auto;
  overflow: hidden;
  border-radius: 14px;
  box-shadow: 0 2px 18px rgba(0, 0, 0, 0.25), 0 16px 16px rgba(0, 0, 0, 0.25);
